.Header{
    width: 100%;
    height: 120px;
    
    display: flex;
    justify-content: center;
    
    z-index: 3;
    background-color: #111111;
  }
  .Header .Header-Section{
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
  }
  .HiddenDropDown, .ActiveDropDown{
    display: none;
  }
 .Header .Header-Section .Drop-Down{
    
  }
  .Header .Header-Section .Header-Section-Logo{
    width: 500px;
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    margin-left: 30px;
  }
  .Header .Header-Section .Header-Section-Logo img{
    height: 80%;
    width: 270px;
  }
  .Header .Header-Section .Header-Section-Nav{
    width: calc(100% - 500px);
    height: 100%;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    gap: 50px;
    margin-right: 50px;
  }
  .Header-Section-Nav a{
    text-decoration: none;
    color: #ffffff;
  }
  .Header-Section-Nav a:visited{
    text-decoration: none;
    color: #ffffff;
  }
  .Header .Header-Section .Header-Section-Nav li {
    list-style: none;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 700;
    font-size: 15px;
    text-transform: uppercase;
    cursor: pointer;
    color: #ffffff;
    transition: color 500ms; /* Added transition property for color change */
  }
  
  .Header .Header-Section .Header-Section-Nav li:last-child {
    background-color: #940000;
    padding: 15px 30px;
    color: white !important;
    outline: 0;
     /* Added transitions */
  }
  
  .Header .Header-Section .Header-Section-Nav li:hover {
    color: #e44c35;
  }
  
  .Header .Header-Section .Header-Section-Nav li:last-child:hover {
    outline: 1px solid white;
  }
  .Header.sticky{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 900000;
    
  }
  .Anim{
    animation: SlideDown 500ms ease-in;
  }
  @keyframes SlideDown{
    0%{
      transform: translateY(-100%);
    }
   
    100%{
      transform: translateY(0%);
    }
  }